 export default {
   data() {
     return {
      data: undefined,
      error: undefined
    }
  },

  created: async function(){

   await this.$axios.post('/videos/all').then((data) => {

     if (!data['data']['data']['status']) {
       this.error = data['data']['data']['message']
     }
     
     this.data = data['data']['data']
   })
  }

}

